var $ = require('jquery');
var apiCalls = require('./apicalls.js');
var validation = require('./searchValidation');

$(document).ready(function() {
  $('#search').submit(function(e) {
    e.preventDefault();
    apiCalls.clearResults();
    var userSearch = $(this).find("input[name='search']").val();
    var validate = validation.validateInput($.trim(userSearch));
    if (!validate) {
      apiCalls.getSongsterr(userSearch);
      apiCalls.getYouTube(userSearch);
      apiCalls.getWiki(userSearch);
      $('.results').show();
      $('.description').hide();
      $('#user-request').val('');
    }
  });
  validation.clearModal();
});



